import constants from './../../contants'

const grandTotalReducer = (state = 0, action) => {
  if (action.for !== constants.for.CLAIMS_ENCODING) return state

  switch (action.type) {
    case 'RESET_GRAND_TOTAL':
      return 0

    case 'SET_GRAND_TOTAL':
      return (state = action.payload)

    default:
      return state
  }
}

export default grandTotalReducer
