import React from 'react'
import Typography from '@material-ui/core/Typography'

import TeethGroup from './TeethGroup'

// Styles
import classes from './DentalChart.module.scss'

const teeth = {
  uppper: {
    left: ['Molar2', 'Molar1', 'Premolar1', 'Canine2', 'Canine1', 'Incisor3', 'Incisor2', 'Incisor1'],
    right: ['Incisor1', 'Incisor2', 'Incisor3', 'Canine1', 'Canine2', 'Premolar1', 'Molar1', 'Molar2']
  },
  lower: {
    left: ['Molar2', 'Premolar2', 'Premolar1', 'Canine1', 'Incisor4', 'Incisor3', 'Incisor2', 'Incisor1'],
    right: ['Incisor1', 'Incisor2', 'Incisor3', 'Incisor4', 'Canine1', 'Premolar1', 'Premolar2', 'Molar2']
  }
}

// props
// states Object {
//  enum: 'filled, extracted, default
// }

export default function DentalChart (props) {
  const { states } = props
  return (
    <div className={classes.root}>
      <div className={classes.title}>
        <Typography variant='h6'>Dental Examination Chart</Typography>
      </div>
      <div className={classes.container}>
        <div className={classes.chart}>
          <div className={classes.upper}>
            <TeethGroup number={1} teeth={teeth.uppper.left} states={states} />
            <TeethGroup number={2} teeth={teeth.uppper.right} reverse states={states} />
          </div>

          <div className={classes.lower}>
            <TeethGroup number={3} teeth={teeth.lower.left} states={states} />
            <TeethGroup number={4} teeth={teeth.lower.right} states={states} />
          </div>
        </div>
      </div>
    </div>
  )
}
