import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from './../ViewList2/ViewList'

const columns = [
  { id: 'verifier-name', name: 'Verifier Name', sortField: 'latest.claim_number' },
  { id: 'total-batches', name: 'Total Batches', sortField: 'latest.date.due' },
  { id: 'total-claims', name: 'Total Claims', sortField: 'latest.facility.name' }
]

const ClaimsVerifierTile = props => {
  const { services } = props

  const [viewList, setViewList] = React.useState({
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'latest.date.due',
    order: true
  })
  const [claimsCount, setClaimsCount] = React.useState(0)

  React.useEffect(() => {
    Promise.all([services.getSubordinatesCount(), services.getClaimsBySubbordinateCount(), getSubordinates()])
      .then(res => {
        const [usersCount, claimsCount] = res
        setViewList(prev => ({ ...prev, count: usersCount.data.data }))
        setClaimsCount(claimsCount.data.data)
      })
      .catch(err => console.error(err.message))
  }, [])

  React.useEffect(() => {
    getSubordinates()
  }, [viewList.limit, viewList.page, viewList.search])

  const getSubordinates = () => {
    services
      .getSubordinates({ page: viewList.page, limit: viewList.limit, search: viewList.search })
      .then(res => {
        const { data, limit, page } = res.data.data
        setViewList(prev => ({
          ...prev,
          data,
          limit,
          page
        }))
      })
      .catch(err => console.error('Error fetching subordinates: ', err))
  }

  const handleOnReviewUser = (params = null) => () => {
    if (!params) return false
    props.history.push(`/list/${params}`)
  }

  const handleOnShowViewList = category => () => {
    let title = 'Claims Verifier List'

    setViewList(prev => ({ ...prev, show: true, title }))

    setViewList(prev => ({
      ...prev,
      data: viewList.data,
      count: viewList.count,
      limit: viewList.limit,
      page: viewList.page
    }))
  }

  const handleOnViewListChange = params => {
    setViewList(prev => ({ ...prev, data: [], ...params }))
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-verifier'
        count={{ color: 3, value: claimsCount, id: 'claims-verifier-count' }}
        sizes={{ sm: 12 }}
        title='Claims Verifier'
        isFilterShown={false}
        viewList={
          <Typography id='cv-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList()}>
            View List
          </Typography>
        }
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`cv-${item.id}`} id={`cv-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {viewList.data &&
              viewList.data.map((item, index) => (
                <TableRow key={`verifier-list-item-${index}`}>
                  <TableCell id={`cv-verifier-name-${index}`}>{item.verifierName}</TableCell>
                  <TableCell id={`cv-total-batches-${index}`}>{item.totalBatches}</TableCell>
                  <TableCell id={`cv-total-claims-${index}`}>{item.totalClaims}</TableCell>
                  <TableCell id={`cv-view-user-${index}`}>
                    <Typography
                      id={`view-user-${index}`}
                      variant='caption'
                      className={`text-link underline`}
                      onClick={handleOnReviewUser(item.id)}
                    >
                      View
                    </Typography>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for Claims Analyst'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={`verifier-list-item-${index}`}>
              <TableCell id={`cv-verifier-name-${index}`}>{item.verifierName}</TableCell>
              <TableCell id={`cv-total-batches-${index}`}>{item.totalBatches}</TableCell>
              <TableCell id={`cv-total-claims-${index}`}>{item.totalClaims}</TableCell>
              <TableCell id={`cv-view-user-${index}`}>
                <Typography
                  id={`view-user-${index}`}
                  variant='caption'
                  className={`text-link underline`}
                  onClick={handleOnReviewUser(item.id)}
                >
                  View
                </Typography>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ClaimsVerifierTile.displayName = 'DashboardTileComponent'

export default ClaimsVerifierTile
