import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim Number', sortField: 'claim.claim_number' },
  { id: 'soa-no', name: 'Bundle/SOA No.', sortField: 'claim.soa_number' },
  { id: 'due-date', name: 'Due Date', sortField: 'claim.date.due' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'claim.facility.name' }
]

const ForPaymentRequestTile = props => {
  const { services, sizes, mTitle } = props

  const [claims, setClaims] = React.useState({ count: 0, data: [] })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getClaims({
        category: 'payment-request',
        limit: 10,
        sort: 'claim.date.due|desc',
        range: 'this-week'
      }),

      services.getClaimCount({ category: 'payment-request' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const paymentRequestClaims = batchResponse.data.data.results
      const paymentRequestClaimCount = batchCountResponse.data.data
      setClaims({ data: paymentRequestClaims, count: paymentRequestClaimCount })
    })
  }, [])

  const handleOnShowViewList = () => {
    const title = 'For Payment Request'
    const category = 'payment-request'
    setViewList({ ...initialValue, show: true, title, category })
    services.getClaims({ category, limit: 10, sort: 'claim.date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleFilterChange = value => {
    const category = 'payment-request'

    services.getClaims({ category, range: value, limit: 10, sort: 'claim.date.due|desc' }).then(({ data: res }) => {
      const { data } = res
      setClaims(state => ({ ...state, data: data.results }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'payment-request',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getClaims(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='for-payment-request'
        count={{ color: 3, value: claims.count, id: 'for-pr-count' }}
        sizes={sizes}
        title={!mTitle ? 'For Payment Request' : mTitle}
        viewList={
          <Typography variant='caption' className='bold text-link' onClick={handleOnShowViewList} id='for-pr-view-list'>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`for-pr-${item.id}`} id={`for-pr-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={index}>
                <TableCell id={`for-pr-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`for-pr-soa-no-${index}`}>{item.soa.number || item.bundle_number}</TableCell>
                <TableCell id={`for-pr-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`for-pr-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell>
                  <Link to={`/payment-request/${item.bundle_number}`}>
                    <Typography variant='caption' className={`text-link underline`} id={`request-payment-${index}`}>
                      Request Payment
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={index}>
              <TableCell id={`for-pr-claim-no-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`for-pr-soa-no-${index}`}>{item.soa.number || item.bundle_number}</TableCell>
              <TableCell id={`for-pr-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
              <TableCell id={`for-pr-facility-name-${index}`}>{item.facility.name}</TableCell>
              <TableCell>
                <Link to={`/payment-request/${item.bundle_number}`}>
                  <Typography variant='caption' className={`text-link underline`} id={`request-payment-${index}`}>
                    Request Payment
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ForPaymentRequestTile.displayName = 'DashboardTileComponent'

export default ForPaymentRequestTile
