import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  approval_code: Joi.string()
    .trim()
    .label('Approval Code')
    .messages({ 'string.empty': `"Approval Code" is required.` })
    .required(),
  loa_number: Joi.string()
    .trim()
    .label('LOA No.')
    .messages({ 'string.empty': `"LOA No." is required.` })
    .required(),
  member_account_number: Joi.string()
    .trim()
    .label('Member Account No.')
    .messages({ 'string.empty': `"Member Account No." is required.` })
    .required(),
  member_last_name: Joi.string()
    .trim()
    .label('Member Last Name')
    .messages({ 'string.empty': `"Member Last Name" is required.` })
    .required(),
  member_first_name: Joi.string()
    .trim()
    .label('Member First Name')
    .messages({ 'string.empty': `"Member First Name" is required.` })
    .required(),
  member_middle_name: Joi.string()
    .trim()
    .label('Member Middle Name')
    .messages({ 'string.empty': `"Member Middle Name" is required.` })
    .when('flag', {
      is: true,
      then: Joi.required(),
      otherwise: Joi.allow('', null)
    }),
  facility_id: Joi.string()
    .trim()
    .label('Medical Partner ID')
    .allow('', null),
  facility_name: Joi.string()
    .trim()
    .label('Medical Partner (Facility) Name')
    .allow('', null),
  date_to: Joi.date()
    .label('Service Date')
    .required()
    .messages({ 'string.empty': `"Service Date" is required.` }),
  date_from: Joi.date()
    .label('Admission Date (From)')
    .required()
    .max('now')
    .messages({
      'date.max': `"Admission Date (From)" should be on or before "Admission Date (To)"`
    })
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
