import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim Number', sortField: 'claim_number', match: 'any' },
  { id: 'due-date', name: 'Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: 'Facility Name', sortField: 'facility_name', match: 'any' }
]

const ClaimsForApprovalTile = props => {
  const { services } = props
  const [claims, setClaims] = React.useState({ count: 0, data: [] })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim_number',
    order: true,
    category: 'for-approval',
    filter: {}
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getItems({
        category: viewList.category,
        limit: viewList.limit,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
        range: 'this-week',
        filter: viewList.filter
      }),
      services.getItemCount({ status: 'FOR APPROVAL' })
    ]).then(values => {
      const [claimResponse, claimCountResponse] = values
      const claims = claimResponse.data.data.results
      const claimsCount = claimCountResponse.data.data
      setClaims({ data: claims, count: claimsCount })
    })
  }, [])

  const handleFilterChange = value => {
    const category = 'for-approval'

    services
      .getItems({
        category,
        limit: 10,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`,
        filter: viewList.filter
      })
      .then(({ data: res }) => {
        const { data } = res

        setClaims(prevState => ({ ...prevState, [category]: { ...prevState[category], data: data.results } }))
      })
  }

  const handleOnShowViewList = () => {
    const category = 'for-approval'
    let title = 'Claims For Approval'

    setViewList({ ...initialValue, show: true, title, category })

    services
      .getItems({
        category,
        limit: 10,
        sort: `${viewList.orderBy}|${viewList.order ? 'asc' : 'desc'}`
      })
      .then(({ data: res }) => {
        const { results, count, limit, page } = res.data
        setViewList(state => ({ ...state, data: results, count, limit, page }))
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'for-approval',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    services.getItems(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-for-approval'
        count={{ color: 3, value: claims['count'], id: 'claims-for-approval-count' }}
        sizes={{ sm: 12 }}
        title='Claims For Approval'
        isFilterShown={false}
        viewList={
          <Typography id='fa-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={item.id} id={`fa-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>

          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={item.claim_number}>
                <TableCell id={`fa-claim-no-${index}`}>{item.claim_number}</TableCell>
                <TableCell id={`fa-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`fa-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell id={`fa-view-claim-${index}`}>
                  <Link to={`/claims/for-approval/${item.claim_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.claim_number}>
              <TableCell id={`claim-number-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
              <TableCell id={`facility-name-${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`view-claim-${index}`}>
                <Link to={`/claims/for-approval/${item.claim_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`view-claim-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ClaimsForApprovalTile.displayName = 'DashboardTileComponent'

export default ClaimsForApprovalTile
