import React from 'react'
import TeethFactory from './Teeth/TeethFactory'

export default function TeethGroup (props) {
  const { number, teeth, states = {} } = props

  const [details, setDetails] = React.useState()

  React.useEffect(() => {
    let _details = {}

    switch (number) {
      case 1:
        _details = {
          startAt: 0,
          transform: {
            flipX: 1
          }
        }
        break

      case 2:
        _details = {
          startAt: 8
        }
        break

      case 3:
        _details = {
          startAt: 31,
          reverse: true,
          transform: {
            flipX: 1,
            flipY: 1
          }
        }
        break

      case 4:
        _details = {
          startAt: 23,
          reverse: true,
          transform: {
            flipY: 1
          }
        }
        break

      default:
        break
    }

    setDetails(_details)
  }, [number])

  return (
    <React.Fragment>
      {details && (
        <React.Fragment>
          {teeth.map((type, index) => {
            const _index = !details.reverse ? details.startAt + index : details.startAt - index
            return (
              <TeethFactory
                key={_index}
                transform={details.transform}
                type={type}
                index={_index}
                state={states[_index]}
              />
            )
          })}
        </React.Fragment>
      )}
    </React.Fragment>
  )
}
