import constants from './../../contants'

export const setGrandTotal = payload => {
  return {
    for: constants.for.CLAIMS_ENCODING,
    type: 'SET_GRAND_TOTAL',
    payload
  }
}

export const resetGrandTotal = () => {
  return {
    for: constants.for.CLAIMS_ENCODING,
    type: 'RESET_GRAND_TOTAL'
  }
}
