import React from 'react'
import { Switch, Route } from 'react-router-dom'

// Pages of Claims Details Encoder Supervisor
import Dashboard from './../Components/ClaimsDetailsEncoderSupervisor/Dashboard/Dashboard'
import CreatedBatchesPage from './../Components/ClaimsDetailsEncoderSupervisor/CreatedBatchesPage/CreatedBatchesPage'
import BatchePage from './../Components/ClaimsDetailsEncoderSupervisor/BatchPage/BatchPage'

// Pages of Claims Details Encoder
import BatchesPage from './../Components/ClaimsDetailsEncoder/BatchesPage/BatchesPage'
import BatchPage from './../Components/ClaimsDetailsEncoder/BatchPage/BatchPage'
import ClaimPage from './../Components/ClaimsDetailsEncoder/ClaimPage/ClaimPage'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ClaimsEncoderSupervisorServices from 'Services/Api/ClaimsEncoderSupervisorServices/ClaimsEncoderSupervisorServices'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/'
  },
  {
    label: 'Batches',
    icon: 'box-open',
    to: '/batches'
  }
]

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  batches: {
    text: 'Batches',
    to: '/batches'
  },
  assigned: {
    text: data => 'Created Batches',
    to: data => `/batches/assigned/${data}`
  },

  specificBatch: {
    text: data => data,
    to: data => `/batches/${data}`
  },
  addClaim: {
    text: 'Add new claim',
    to: ''
  },
  updateClaim: {
    text: 'Update Claim',
    to: ''
  }
}

function ClaimsDetailsEncoderSupervisorRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <Switch>
      <Route path='/' exact component={Dashboard} />
      <Route path='/batches' exact component={BatchesPage} />
      <Route
        path='/batches/:_batchNumber'
        exact
        render={props => <BatchPage {...props} apiService={ClaimsEncoderSupervisorServices} />}
      />
      <Route path='/batches/assigned/:_id' exact component={CreatedBatchesPage} />
      <Route path='/batches/assigned/:_id/:_batchNumber' exact component={BatchePage} />
      <Route
        path='/batches/:_batchNumber/claims/add'
        exact
        render={props => <ClaimPage {...props} isAdd apiService={ClaimsEncoderSupervisorServices} />}
      />
      <Route
        path='/batches/:_batchNumber/claims/:_claimNumber'
        exact
        render={props => <ClaimPage {...props} apiService={ClaimsEncoderSupervisorServices} />}
      />
    </Switch>
  )
}

export default ClaimsDetailsEncoderSupervisorRoutes
