import React from 'react'

import Incisor1 from './Incisor/1'
import Incisor2 from './Incisor/2'
import Incisor3 from './Incisor/3'
import Incisor4 from './Incisor/4'
import Canine1 from './Canine/1'
import Canine2 from './Canine/2'
import Premolar1 from './Premolar/1'
import Premolar2 from './Premolar/2'
import Molar1 from './Molar/1'
import Molar2 from './Molar/2'

import classes from './style.module.scss'

export default function TeethFactory (props) {
  const { type, index, transform = {}, state = [] } = props

  return (
    <div className={classes.root}>
      {transform.flipY && (
        <React.Fragment>
          <div className={classes['other-details']}>
            <div className={classes.circle} />
            <span>{index + 1}</span>
          </div>
        </React.Fragment>
      )}

      <div
        className={`${classes.teeth} ${classes[state]}  ${transform.flipX && classes['flip-x']} ${transform.flipY &&
          classes['flip-y']}`}
      >
        <React.Fragment>
          {type === 'Incisor1' && <Incisor1 state={state} />}
          {type === 'Incisor2' && <Incisor2 state={state} />}
          {type === 'Incisor3' && <Incisor3 state={state} />}
          {type === 'Incisor4' && <Incisor4 state={state} />}

          {type === 'Canine1' && <Canine1 state={state} />}
          {type === 'Canine2' && <Canine2 state={state} />}

          {type === 'Premolar1' && <Premolar1 state={state} />}
          {type === 'Premolar2' && <Premolar2 state={state} />}

          {type === 'Molar1' && <Molar1 state={state} />}
          {type === 'Molar2' && <Molar2 state={state} />}
        </React.Fragment>
      </div>
      {!transform.flipY && (
        <React.Fragment>
          <div className={classes['other-details']}>
            <span>{index + 1}</span>
            <div className={classes.circle} />
          </div>
        </React.Fragment>
      )}
    </div>
  )
}
