import instance from './instance'
const axios = instance('/c/prs')

export default class PaymentRequestServices {

  static getBundleCount () {
    return axios({
      url: `/payment-requests/submitted/count`,
      method: 'GET'
    })
  }

  static getSubmittedPaymentRequest (params) {
    return axios({
      url: `/payment-requests/submitted`,
      method: 'GET',
      params
    })
  }

  static getPaymentRequestBundles ({ processor_id }, params) {
    return axios({
      url: `/payment-requests/${processor_id}/submitted`,
      method: 'GET',
      params
    })
  }
}
