import React from 'react'
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'batch-number', name: ' Batch Number', sortField: 'batch_number', match: 'start' },
  { id: 'due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' }
]

const OpenBatchesTile = props => {
  const { services, sizes } = props

  const [batches, setBatches] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    Promise.all([
      services.getBatches({
        category: 'open',
        limit: 10,
        sort: 'date.due|desc',
        range: 'this-week'
      }),
      services.getBatchCount({ category: 'open' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const openBatches = batchResponse.data.data.results
      const openBatchCount = batchCountResponse.data.data
      setBatches({ data: openBatches, count: openBatchCount })
    })
  }, [])

  const handleFilterChange = value => {
    const category = 'open'
    services.getBatches({ category, range: value, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { data } = res
      setBatches(state => ({ ...state, data: data.results }))
    })
  }

  const handleOnEncode = batch_number => {
    services.assignBatch({ batch_number }).then(({ data: res }) => {
      props.history.push(`/batches/${batch_number}`)
    })
  }

  const handleOnShowViewList = () => {
    const category = 'open'
    setViewList({ ...initialValue, show: true, title: `Open Batches`, category })
    services.getBatches({ category, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'open',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    services.getBatches(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='open-batches'
        sizes={sizes}
        count={{ color: 3, value: batches.count, id: 'open-batches-count' }}
        title='Open Batches'
        viewList={
          <Typography id='ob-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`op-${item.id}`} id={`op-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {batches.data.map((item, index) => (
              <TableRow key={item.batch_number}>
                <TableCell id={`ob-batch-no-${index}`}>{item.batch_number}</TableCell>
                <TableCell id={`ob-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`ob-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell>
                  <Typography
                    id={`encode-batch-${index}`}
                    variant='caption'
                    className={`text-link underline`}
                    onClick={() => handleOnEncode(item.batch_number)}
                  >
                    Encode
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for batch number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.batch_number}>
              <TableCell id={`batch-number-${index}`}>{item.batch_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
              <TableCell id={`facility-name=${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`encode-batch-${index}`}>
                <Typography
                  variant='caption'
                  className='text-link underline'
                  onClick={() => handleOnEncode(item.batch_number)}
                >
                  Encode
                </Typography>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

OpenBatchesTile.displayName = 'DashboardTileComponent'

export default OpenBatchesTile
