import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'batch-number', name: ' Batch Number', sortField: 'batch_number', match: 'start' },
  { id: 'due-date', name: ' Due Date', sortField: 'date_due', match: 'date' },
  { id: 'facility-name', name: ' Medical Partner Name (Facility)', sortField: 'facility_name', match: 'any' },
  { id: 'status', name: ' Status', sortField: 'status', match: '' }
]

const MyBatchesTile = props => {
  const { services, sizes } = props

  const [batches, setBatches] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    //* My Batches
    Promise.all([
      services.getBatches({
        category: 'my',
        limit: 10,
        sort: 'date.due|desc',
        range: 'this-week'
      }),
      services.getBatchCount({ category: 'my' })
    ]).then(values => {
      const [batchResponse, batchCountResponse] = values
      const myBatches = batchResponse.data.data.results
      const myBatchCount = batchCountResponse.data.data
      setBatches({ data: myBatches, count: myBatchCount })
    })
  }, [])

  const handleFilterChange = value => {
    const category = 'my'
    services.getBatches({ category, range: value, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { data } = res
      setBatches(state => ({ ...state, data: data.results }))
    })
  }

  const handleOnShowViewList = () => {
    const category = 'my'
    setViewList({ ...initialValue, show: true, title: `My Batches`, category })
    services.getBatches({ category, limit: 10, sort: 'date.due|desc' }).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  const handleOnViewListChange = params => {
    const _params = {
      category: 'my',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }

    services.getBatches(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='my-batches'
        sizes={sizes}
        count={{ color: 8, value: batches.count, id: 'my-batches-count' }}
        title='My Batches'
        viewList={
          <Typography id='mb-view-list' variant='caption' className='bold text-link' onClick={handleOnShowViewList}>
            View List
          </Typography>
        }
        onFilterChange={handleFilterChange}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`mb-${item.id}`} id={`mb-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {batches.data.map((item, index) => (
              <TableRow key={item.batch_number}>
                <TableCell id={`mb-batch-no-${index}`}>{item.batch_number}</TableCell>
                <TableCell id={`mb-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
                <TableCell id={`mb-facility-name-${index}`}>{item.facility.name}</TableCell>
                <TableCell id={`mb-status-${index}`}>In Progress</TableCell>
                <TableCell>
                  <Link to={`/batches/${item.batch_number}`}>
                    <Typography variant='caption' className='text-link underline' id={`update-batch-${index}`}>
                      Update
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns.filter(i => i.id !== 'status')}
        searchTextPlaceHolder='Search for batch number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.batch_number}>
              <TableCell id={`batch-number-${index}`}>{item.batch_number}</TableCell>
              <TableCell id={`due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
              <TableCell id={`facility-name=${index}`}>{item.facility.name}</TableCell>
              <TableCell id={`encode-batch-${index}`}>
                <Link to={`/batches/${item.batch_number}`}>
                  <Typography variant='caption' className='text-link underline' id={`update-batch-${index}`}>
                    Update
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

MyBatchesTile.displayName = 'DashboardTileComponent'

export default MyBatchesTile
