import Joi from '@hapi/joi'

// This if for claim types that have `Approval Code` and `Loa No.` as required fields.
export default function (selected_fields) {
  const available_fields = {
    approval_code: Joi.string()
      .trim()
      .label('Approval Code')
      .messages({ 'string.empty': `"Approval Code" is required.` })
      .required(),
    loa_number: Joi.string()
      .trim()
      .label('LOA No.')
      .messages({ 'string.empty': `"LOA No." is required.` })
      .required(),
    claim_source: Joi.string()
      .label('Claim Source')
      .required(),
    member_account_number: Joi.string()
      .trim()
      .label('Member Account No.')
      .messages({ 'string.empty': `"Member Account No." is required.` })
      .required(),
    member_last_name: Joi.string()
      .trim()
      .label('Member Last Name')
      .messages({ 'string.empty': `"Member Last Name" is required.` })
      .required(),
    member_first_name: Joi.string()
      .trim()
      .label('Member First Name')
      .messages({ 'string.empty': `"Member First Name" is required.` })
      .required(),
    member_middle_name: Joi.string()
      .trim()
      .label('Member Middle Name')
      .messages({ 'string.empty': `"Member Middle Name" is required.` })
      .when('flag', {
        is: true,
        then: Joi.required(),
        otherwise: Joi.allow('', null)
      }),
    client_account_number: Joi.string()
      .label('Client Account Number')
      .required(),
    client_account_name: Joi.string()
      .label('Client Account Number')
      .required(),
    customer_account_number: Joi.string()
      .label('Customer (Company) Account Number')
      .required(),
    company_name: Joi.string()
      .label('Company Name')
      .required(),
    facility_id: Joi.string()
      .trim()
      .label('Medical Partner ID')
      .messages({ 'string.empty': `"Medical Partner ID" is required.` })
      .required(),
    facility_name: Joi.string()
      .trim()
      .label('Medical Partner (Facility) Name')
      .messages({ 'string.empty': `"Medical Partner (Facility) Name" is required.` })
      .required(),
    date_to: Joi.date()
      .label('Service Date')
      .required()
      .messages({ 'string.empty': `"Service Date" is required.` }),
    date_from: Joi.date()
      .label('Admission Date (From)')
      .required()
      .max('now')
      .messages({
        'date.max': `"Admission Date (From)" should be on or before "Admission Date (To)"`
      }),
    medicine_category: Joi.string()
      .trim()
      .label('Medicine Category')
      .messages({ 'string.empty': `"Medicine Category" is required.` })
      .required()
  }

  const result = {}
  for (const field of selected_fields) {
    result[field] = available_fields[field]
  }

  if (result['date_from'] && !result['date_to']) {
    result['date_from'] = Joi.date()
      .label('Date of Service')
      .required()
      .max('now')
  }
  return result
}
