import constants from './../../contants'

export const initDropDowns = payload => {
  return {
    for: constants.for.CLAIMS_ENCODING,
    type: 'INIT_DROP_DOWNS',
    payload
  }
}

export const setDropDown = payload => {
  return {
    for: constants.for.CLAIMS_ENCODING,
    type: 'SET_DROP_DOWN',
    payload
  }
}
