import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  claim_type: Joi.string()
    .trim()
    .label('Claim Type')
    .required(),
  claim_source: Joi.string()
    .trim()
    .label('Claim Source')
    .required(),
  total_rcs: Joi.number()
    .label('Total No. of RCS')
    .integer()
    .min(0)
    .required(),
  amount_billed: Joi.number()
    .label('Billed Amount')
    .less(100000000)
    .greater(-1)
    .messages({
      'number.less': `Billed Amount should be less than {#limit}`,
      'number.greater': 'Billed Amount should be greater than 0'
    }),
  notes: Joi.string()
    .trim()
    .label('Notes/Comments')
    .allow('', null),
  doctor_id: Joi.string()
    .when('claim_type', { is: 'Out Patient Consultation', then: Joi.required(), otherwise: Joi.allow('', null) })
    .trim()
    .label('Medical Partner ID')
    .messages({ 'string.empty': `"Medical Partner ID" is required.` }),
  doctor_name: Joi.string()
    .when('claim_type', { is: 'Out Patient Consultation', then: Joi.required(), otherwise: Joi.allow('', null) })
    .trim()
    .label('Medical Partner (Doctor) Name')
    .messages({ 'string.empty': `"Medical Partner (Doctor) Name" is required.` })
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
