import Joi from '@hapi/joi'

import getValidators from './getValidators'
import getValidators2 from './getValidators2'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const validate = fields => (data, type = false) => {
  let schema = Joi.object(getValidators(fields)).options({ abortEarly: false })
  // FOR Claim Types that have `Approval Code` and `LOA No.` as required fields.
  if (type) schema = Joi.object(getValidators2(fields)).options({ abortEarly: false })
  return ExtractValidationResult(schema.validate(data))
}

export default validate
