import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import startCase from 'lodash/startCase'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

const columns = [
  { id: 's-encoder-name', name: ' Encoder Name', sortField: 'username', match: 'start' },
  { id: 's-total-batches', name: ' Total Batches', sortField: 'total_batches', match: 'any' },
  { id: 's-total-rcs', name: ' Total No. of RCS', sortField: 'total_rcs', match: 'any' }
]
const ClaimsEncoderTile = props => {
  const { services, sizes = { sm: 12 } } = props

  const [batches, setBatches] = React.useState({
    count: 0,
    data: []
  })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    filter: {},
    data: [],
    orderBy: 'name',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {
    getEncoders('CLAIMS_DETAILS_ENCODER')
  }, [])

  const getEncoders = role => {
    services
      .getEncodersBatches({ role, param: { limit: 10, sort: 'name|asc' } })
      .then(res => {
        const { result } = res.data.data.data
        const { count } = res.data.data
        setBatches(state => ({ ...state, count, data: result }))
      })
      .catch(err => {
        console.error(err)
        setBatches(state => ({ ...state, count: 0, data: [] }))
      })
  }

  const handleOnShowViewList = category => () => {
    setViewList({ ...initialValue, show: true, title: startCase(`${category} Batches`), category })
    services
      .getEncodersBatches({ role: 'CLAIMS_DETAILS_ENCODER', param: { limit: 10, sort: 'name|asc' } })
      .then(({ data: res }) => {
        const { result, count } = res.data.data
        setViewList(state => ({ ...state, data: result, count }))
        // setViewList(state => ({ ...state, page, limit }))
      })
  }

  const handleOnViewListChange = params => {
    const _params = {
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      filter: viewList.filter,
      ...params
    }
    services.getEncodersBatches({ role: 'CLAIMS_DETAILS_ENCODER', param: { ..._params } }).then(({ data: res }) => {
      const { result, count } = res.data.data
      const { limit, page } = res.data
      setViewList(state => ({ ...state, data: result, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='claims-encoder'
        count={{ color: 3, value: batches.count, id: 'claims-encoder-count' }}
        isFilterShown={false}
        title='Claims Encoder'
        sizes={sizes}
        viewList={
          <Typography
            id='ob-view-list'
            variant='caption'
            className='bold text-link'
            onClick={handleOnShowViewList('claim-encoder')}
          >
            View List
          </Typography>
        }
        onFilterChange={() => {}}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`ces-${item.id}`} id={`ces-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}

              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {batches.data.map((item, index) => (
              <TableRow key={index}>
                <TableCell id={`ces-encoder-name-${index}`}>{item.username}</TableCell>
                <TableCell id={`ces-total-batches-${index}`}>{item.total_batches}</TableCell>
                <TableCell id={`ces-total-rcs-${index}`}>{item.total_rcs}</TableCell>
                <TableCell>
                  <Link to={`/batches/assigned/${item.id}`}>
                    <Typography variant='caption' className='text-link underline' id={`view-encoder-${index}`}>
                      View
                    </Typography>
                  </Link>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for encoder name, total bundle or total batch'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={item.id}>
              <TableCell id={`batch-number-${index}`}>{item.username}</TableCell>
              <TableCell id={`date-received-${index}`}>{item.total_batches}</TableCell>
              <TableCell id={`due-date-${index}`}>{item.total_rcs}</TableCell>
              <TableCell id={`view-encoder-${index}`}>
                <Link to={`/batches/assigned/${item.id}`}>
                  <Typography variant='caption' className='text-link underline' id={`view-encoder-${index}`}>
                    View
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

ClaimsEncoderTile.displayName = 'DashboardTileComponent'

export default ClaimsEncoderTile
