export const resetInquiry = payload => {
  return {
    type: 'RESET_INQUIRY_MESSAGES',
    payload
  }
}

export const addInquiryMessages = payload => {
  return {
    type: 'ADD_INQUIRY_MESSAGES',
    payload
  }
}

export const setInquiryMessages = payload => {
  return {
    type: 'SET_INQUIRY_MESSAGES',
    payload
  }
}

export const setInquiryMessage = payload => {
  return {
    type: 'SET_INQUIRY_MESSAGE',
    payload
  }
}

export const setInquiryAssignee = payload => {
  return {
    type: 'SET_INQUIRY_ASSIGNEE',
    payload
  }
}
