import React from 'react'
import Typography from '@material-ui/core/Typography'
import { Link } from 'react-router-dom'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'

// Components
import DashboardTile from 'SharedComponents/DashboardTileComponent/DashboardTileComponent'
import ViewList from '../ViewList2/ViewList'

// Utilities
import { DateFormatter } from 'Utilities/TextFormatter'

const columns = [
  { id: 'claim-number', name: 'Claim Number', sortField: 'claim.claim_number' },
  { id: 'soa-no', name: 'Bundle/SOA No.', sortField: 'claim.soa_number' },
  { id: 'due-date', name: 'Due Date', sortField: 'claim.date.due' },
  { id: 'facility-name', name: 'Medical Partner Name (Facility)', sortField: 'claim.facility.name' }
]

const OverrideClaimTile = props => {
  const { services, sizes } = props

  const [claims] = React.useState({ count: 0, data: [] })

  const initialValue = {
    show: false,
    title: '',
    count: 0,
    search: '',
    limit: 10,
    page: 1,
    data: [],
    orderBy: 'claim.date.due',
    order: true
  }

  const [viewList, setViewList] = React.useState(initialValue)

  React.useEffect(() => {}, [])

  const handleOnViewListChange = params => {
    const _params = {
      category: 'payment-request',
      limit: viewList.limit,
      page: viewList.page,
      search: viewList.search,
      sort: `${viewList.orderBy}|${viewList.order ? 'desc' : 'asc'}`,
      ...params
    }

    services.getClaims(_params).then(({ data: res }) => {
      const { results, count, limit, page } = res.data
      setViewList(state => ({ ...state, data: results, count, limit, page }))
    })
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='override-claim'
        count={{ color: 5, value: claims.count, id: 'override-claim-count' }}
        sizes={sizes}
        title='Override Claim'
        viewAllLink={{ to: '/claims?type=override', caption: 'View List' }}
      >
        <Table size='small'>
          <TableHead>
            <TableRow>
              {columns.map(item => (
                <TableCell key={`oc-${item.id}`} id={`oc-${item.id}`}>
                  {item.name}
                </TableCell>
              ))}
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {claims.data.map((item, index) => (
              <TableRow key={index}>
                <TableCell>{item.soa_no}</TableCell>
                <TableCell>{item.claim_no}</TableCell>
                <TableCell>{item.due_date}</TableCell>
                <TableCell>{item.facility_name}</TableCell>
                <TableCell>
                  <Typography variant='caption' className={`text-link underline`}>
                    Request Payment
                  </Typography>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </DashboardTile>

      <ViewList
        columns={columns}
        searchTextPlaceHolder='Search for claim number, date or facility name'
        viewList={viewList}
        setViewList={setViewList}
        onViewListChange={handleOnViewListChange}
        body={_data =>
          _data.map((item, index) => (
            <TableRow key={index}>
              <TableCell id={`oc-claim-no-${index}`}>{item.claim_number}</TableCell>
              <TableCell id={`oc-soa-no-${index}`}>{item.soa.number || item.bundle_number}</TableCell>
              <TableCell id={`oc-due-date-${index}`}>{DateFormatter(item.date.due)}</TableCell>
              <TableCell id={`oc-facility-name-${index}`}>{item.facility.name}</TableCell>
              <TableCell>
                <Link to={`/payment-request/${item.bundle_number}`}>
                  <Typography variant='caption' className={`text-link underline`} id={`request-payment-${index}`}>
                    Request Payment
                  </Typography>
                </Link>
              </TableCell>
            </TableRow>
          ))
        }
      />
    </React.Fragment>
  )
}

OverrideClaimTile.displayName = 'DashboardTileComponent'

export default OverrideClaimTile
