const Item = item => {
  // fields from Encoder
  const { gross_amount = 0, vat_amount = 0, amount_paid_by_member = 0, philhealth_amount_paid = 0 } = item
  // fields from Analyst
  const { amount_not_covered = 0, amount_due_from_member = 0 } = item

  return (
    parseFloat(gross_amount) +
    parseFloat(vat_amount) -
    (parseFloat(amount_not_covered) +
      parseFloat(philhealth_amount_paid) +
      parseFloat(amount_due_from_member) +
      parseFloat(amount_paid_by_member))
  )
}

const List = items => {
  return items.reduce((current, item) => current + Item(item), 0)
}

const Grand = lineItems => {
  const { procedures, dental_procedures, hospital_items, professional_fees } = lineItems || {}
  let grand = 0
  if (procedures) grand += List(procedures)
  if (dental_procedures) grand += List(dental_procedures)
  if (hospital_items) grand += List(hospital_items)
  if (professional_fees) grand += List(professional_fees)
  return grand
}

export default { Item, List, Grand }
