function ClaimsManagerRoutes () {
  SideBarService.setItems(sideBarItems)
  BreadcrumbsService.setItems(breadcrumbs)

  return (
    <React.Fragment>
      <Switch>
        <Route path='/' exact component={Dashboard} />
        <Route path='/batch-encoder-work/:user_id' exact component={CreatedBundlesPage} />
        <Route path='/batch-encoder-work/:user_id/bundles/:bundle_number' exact component={BundlePage} />
        <Route path='/batches/assigned/:_id' exact component={CreatedBatchesPage} />
        <Route path='/batches/assigned/:_id/:_batchNumber' exact component={BatchePage} />
        <Route path='/list/:user_id' exact component={ClaimsList} />
        <Route path='/payment-request/:bundle_number' exact component={PaymentRequestPage} />

        {/* PR Supervisor Pages */}
        <Route path='/payment-request/:bundle_number/submitted' exact component={SubmittedPaymentRequestPage} />
        <Route path='/payment-requests/:pr_officer/list' exact component={SubmittedPaymentRequestList} />
        {/* PR Supervisor Pages */}

        <Provider store={Store}>
          <Route
            path='/claims/for-approval/:claim_number'
            exact
            render={props => (
              <ClaimsViewer
                breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'forApproval']}
                {...props}
                permissions={forApprovalPermissions}
                ApiService={ClaimsVerifierSupervisorServices}
              />
            )}
          />

          <Route
            path='/claims/reimbursement/:claim_number'
            exact
            render={props => (
              <ClaimsViewer
                breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'reimbursement']}
                {...props}
                permissions={rejectedPermissions}
                ApiService={ClaimsVerifierSupervisorServices}
              />
            )}
          />

          <Route
            path='/claims/recon/:claim_number'
            exact
            render={props => (
              <ClaimsViewer
                breadCrumbsList={['claims', `specificClaim:${props.match.params.claim_number}`, 'recon']}
                {...props}
                permissions={permissions}
                ApiService={ClaimsReconServices}
              />
            )}
          />
        </Provider>
      </Switch>
    </React.Fragment>
  )
}

export default ClaimsManagerRoutes
