import React from 'react'
import Paper from '@material-ui/core/Paper'

// Components
import AutoComplete from 'SharedComponents/AutoComplete/AutoComplete'
import TextField from 'SharedComponents/TextField/CustomTextField'
import ClaimFooter from '../ClaimFooter/ClaimFooter'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import DisapprovedLabel from '../DisapprovedLabel/DisapprovedLabel'
import DiscrepancyBase from '../DiscrepancyBase/DiscrepancyBase'
import HospitalItemList from '../HospitalItemList/HospitalItemList'
import ProcedureList from '../ProcedureList/ProcedureList'
import ProfessionalFeeList from '../ProfessionalFeeList/ProfessionalFeeList'
import RightPanelTabs from '../RightPanelTabs/RightPanelTabs'
import Typography from '@material-ui/core/Typography'

// Controller
import ConfigController from 'Controllers/ConfigController2'

// Services
import ClaimsAdjudicationServices from 'Services/Api/ClaimsAdjudicationServices'

// Utilities
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './ClaimClinicManagement.module.scss'

// Validator
import MainValidator from './../../Validators/Claim'
import HospitalItemValidator from '../HospitalItemForm/HospitalItemValidator'
import ProfessionalFeeValidator from '../ProfessionalFeeForm/ProfessionalFeeValidator'
import ProcedureValidator from '../ProcedureForm/ProcedureValidator'

// Store
import { useSelector, useDispatch } from 'react-redux'
import {
  setClaimFields,
  setClaimBaseDetails,
  setDropDown,
  initDropDowns,
  initSuggestions,
  setProcedure,
  setHospitalItem,
  setProfessionalFee,
  resetError,
  setError
} from '../../Store/actions'

export default function ClaimClinicManagement (props) {
  const dispatch = useDispatch()
  // Events
  const {
    onEscalate,
    onViewAnalysisSheet,
    onViewLetterOfAdvice,
    onReject,
    onReassign,
    onApprove,
    onSave,
    onCancel,
    onChangeFacility
  } = props
  // Variables
  const { claim, permissions, _type, grandTotal } = props
  // Services
  const { getClaimDetails, getUsers, userManagementService = () => {} } = props

  const suggestions = useSelector(state => state.suggestions)
  const discrepancy = useSelector(state => state.discrepancy)
  const error = useSelector(state => state.error)
  const escalations = useSelector(state => state.escalations)
  const inquiries = useSelector(state => state.inquiries)
  const recon = useSelector(state => state.recon)

  const [hasError, setHasError] = React.useState(false)
  const [allowedFields, setAllowedFields] = React.useState({})

  React.useEffect(() => {
    dispatch(initSuggestions())
    dispatch(initDropDowns(['reason']))
    dispatch(initDropDowns(['reason-charging']))

    getReason()

    const _allowedFields = {
      client_account_name: '',
      client_account_number: '',
      date_from: new Date().toISOString(),
      date_to: new Date().toISOString()
    }

    const _lineItemFields = {
      procedures: [],
      professional_fees: [],
      hospital_items: []
    }

    setTimeout(() => {
      setAllowedFields(_allowedFields)
      dispatch(setClaimFields({ ..._allowedFields, ..._lineItemFields }))
    }, 10)

    getClaimDetails(data => {
      dispatch(
        setClaimFields({
          client_account_name: data.client.account_name,
          client_account_number: data.client.account_number,
          date_from: new Date(data.date.to).toISOString(),
          date_to: new Date(data.date.to).toISOString(),
          procedures: data.procedures || [],
          hospital_items: data.hospital_items || [],
          professional_fees: data.professional_fees || []
        })
      )
      ConfigController.getUserManagementData().then(res => {
        dispatch(
          setClaimBaseDetails({
            value: new Date().toISOString(),
            field: 'approved_date'
          })
        )
        dispatch(
          setClaimBaseDetails({
            value: res.user_id,
            field: 'approved_id'
          })
        )
        dispatch(
          setClaimBaseDetails({
            value: `${res.first_name} ${res.last_name}`,
            field: 'approved_name'
          })
        )
      })
    })
  }, [])

  const getReason = () => {
    ClaimsAdjudicationServices.lookups({ on: 'dropdown', params: { field: 'reason' } }).then(res => {
      const { data } = res.data
      const reason = data.map(item => ({
        text: item.reason,
        value: item.reason,
        code: item.code
      }))
      dispatch(setDropDown({ reason }))
    })
    ClaimsAdjudicationServices.lookups({ on: 'dropdown', params: { field: 'reason-charging' } }).then(res => {
      const { data } = res.data
      const reason_charging = data.map(item => ({
        text: item.reason_charging,
        value: item.reason_charging,
        code: item.code
      }))
      dispatch(setDropDown({ reason_charging }))
    })
  }

  const handleOnChangeFacility = args => {
    onChangeFacility(args)
  }

  const doValidateBase = () => {
    const data = {}
    const { diagnosis, ...fields } = allowedFields
    for (let f in fields) data[f] = claim[f]

    const { error = {} } = MainValidator(Object.keys(fields))({ ...data }, true)
    return error
  }

  const doValidateProcedure = ({ details }) => {
    const { error } = ProcedureValidator({
      description: details.description,
      code: details.code,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateHospitalItem = ({ details }) => {
    const { error } = HospitalItemValidator({
      name: details.name,
      item_code: details.item_code,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateProfessionalFee = ({ details }) => {
    const { error } = ProfessionalFeeValidator({
      doctor_id: details.doctor_id,
      doctor_name: details.doctor_name,
      specialization: details.specialization,
      gross_amount: details.gross_amount,
      vat_amount: details.vat_amount,
      amount_paid_by_member: details.amount_paid_by_member,
      philhealth_amount_paid: details.philhealth_amount_paid,
      philhealth_remarks: details.philhealth_remarks,
      amount_not_covered: details.amount_not_covered,
      reason: details.reason,
      amount_due_from_member: details.amount_due_from_member,
      reason_for_charging_member: details.reason_for_charging_member
    })
    return error
  }

  const doValidateAll = () => {
    dispatch(resetError())

    const base_errors = doValidateBase()
    let procedure = {}
    let hospital_item = {}
    let professional_fee = {}

    for (const [index, value] of claim.procedures.entries()) {
      const error = doValidateProcedure({ index, details: value })
      if (error) {
        procedure = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setProcedure({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }

    for (const [index, value] of claim.hospital_items.entries()) {
      const error = doValidateHospitalItem({ index, details: value })
      if (error) {
        hospital_item = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setHospitalItem({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }

    for (const [index, value] of claim.professional_fees.entries()) {
      const error = doValidateProfessionalFee({ index, details: value })
      if (error) {
        professional_fee = { index, ...error }
        // AUTO OPEN CLAIM
        dispatch(
          setProfessionalFee({
            show: true,
            mode: 'edit',
            index,
            details: value
          })
        )
        break
      }
    }
    dispatch(
      setError({
        ...base_errors,
        procedure,
        hospital_item,
        professional_fee
      })
    )

    const invalid =
      Object.keys(base_errors).length > 0 ||
      procedure.index >= 0 ||
      hospital_item.index >= 0 ||
      professional_fee.index >= 0

    setHasError(invalid)

    return invalid
  }

  React.useEffect(() => {
    if (hasError) {
      console.group('Validation Failed')
      for (const value in error) {
        console.log(value, error[value])
      }
      console.groupEnd()
    }
  }, [hasError])

  return (
    <div className={classes.root}>
      {claim && (
        <React.Fragment>
          <DisapprovedLabel
            id='dp-failed-adjudication'
            value='Failed Adjudication'
            reasons={discrepancy.disapproved ? discrepancy.disapproved : []}
          />
          {permissions.base.read && (
            <Paper className={classes['first-container']}>
              <div className={classes.groups}>
                <div className={classes['input']}>
                  <TextField
                    id='client-account-number'
                    label='Client Account Number'
                    value={claim.client_account_number}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'client_account_number'
                        })
                      )
                    }
                    error={error.client_account_number}
                    readOnly={!permissions.base.update}
                  />
                </div>

                <div className={classes['input']}>
                  <TextField
                    id='client-account-name'
                    label='Client Account Name'
                    value={claim.client_account_name}
                    onChange={event =>
                      dispatch(
                        setClaimBaseDetails({
                          value: event.target.value,
                          field: 'client_account_name'
                        })
                      )
                    }
                    error={error.client_account_number}
                    readOnly={!permissions.base.update}
                  />
                </div>
              </div>
              <div className={classes.groups}>
                <div className={classes['input']}>
                  <DatePicker
                    id='admission-date-from'
                    required
                    label='Admission Date (From)'
                    value={claim.date_from}
                    onChange={event => {
                      dispatch(
                        setClaimBaseDetails({
                          value: event.toISOString(),
                          field: 'date_from'
                        })
                      )
                    }}
                    error={error.date_from}
                    readOnly={!permissions.base.update}
                  />
                </div>
                <div className={classes['input']}>
                  <DatePicker
                    id='admission-date-to'
                    required
                    label='Admission Date (To)'
                    value={claim.date_to}
                    onChange={event => {
                      dispatch(
                        setClaimBaseDetails({
                          value: event.toISOString(),
                          field: 'date_to'
                        })
                      )
                    }}
                    error={error.date_to}
                    readOnly={!permissions.base.update}
                  />
                </div>
              </div>
            </Paper>
          )}

          <div className={classes['tables-container']}>
            <div className={classes['left-side']} id='left-side-panel'>
              {permissions.procedures.read && (
                <ProcedureList
                  id='procedure'
                  data={claim.procedures}
                  doValidate={doValidateProcedure}
                  permissions={permissions.procedures}
                  showAmountPaidByMember={false}
                  showAmountNotCovered={false}
                  showAmountDueFromMember={false}
                  showPhilhealthField={false}
                />
              )}
              {permissions.hospital_items.read && (
                <HospitalItemList
                  id='hospital-item'
                  data={claim.hospital_items}
                  doValidate={doValidateHospitalItem}
                  permissions={permissions.hospital_items}
                  showAmountPaidByMember={false}
                  showAmountNotCovered={false}
                  showAmountDueFromMember={false}
                />
              )}
              {permissions.professional_fees.read && (
                <ProfessionalFeeList
                  id='professional-fee'
                  data={claim.professional_fees}
                  doValidate={doValidateProfessionalFee}
                  permissions={permissions.professional_fees}
                  showAmountNotCovered={false}
                  showAmountDueFromMember={false}
                />
              )}
            </div>
            <div className={classes['right-side']} id='right-side-panel'>
              <div className={classes['wrapper']}>
                <div className={classes['sticky']}>
                  <RightPanelTabs inquiries={inquiries} escalations={escalations} recon={recon} {...props} />
                </div>
              </div>
            </div>
          </div>
          <div className={classes['grand-total']}>
            <Typography variant='subtitle2'>TOTAL PHP </Typography>
            <Typography id={'grand-total'} variant='subtitle2'>
              {CurrencyFormatter(grandTotal)}
            </Typography>
          </div>
          <ClaimFooter
            getUsers={getUsers}
            doValidate={doValidateAll}
            doEscalate={onEscalate}
            doReassign={onReassign}
            doViewAnalysisSheet={onViewAnalysisSheet}
            doViewLetterOfAdvice={onViewLetterOfAdvice}
            doReject={onReject}
            doApprove={onApprove}
            doSave={onSave}
            doCancel={onCancel}
            permissions={permissions.footer}
            grandTotal={grandTotal}
            claimType={_type}
            userManagementService={userManagementService}
          />
        </React.Fragment>
      )}
    </div>
  )
}
